@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;1,600&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}.app{background:#f2f1f2;max-width:100vw;min-height:100vh;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;position:relative;font-family:'Open Sans', sans-serif;overflow:hidden}.center-items{display:grid;justify-items:center;align-items:center}input{outline:none}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}.fade-in{animation:fadeIn 1s ease-in-out}.header{height:10vh;width:100%;grid-template-columns:repeat(2, 1fr);background:#fff;box-shadow:0px 0px 5px -3px #000;grid-gap:1em}@media screen and (max-width: 425px){.header{grid-template-columns:1fr 2fr}}.header .title{font-size:30px;margin-left:1em}@media screen and (max-width: 425px){.header .title{font-size:1.2em}}.middle{margin:5em;grid-template-columns:repeat(auto-fit, minmax(20rem, 1fr));grid-gap:3em}@media screen and (max-width: 555px){.middle{margin:3em}}@media screen and (max-width: 425px){.middle{margin:1em}}.middle .search-not-found{font-size:30px}.contact-card{background:#fff;width:220px;height:300px;padding:1em;font-size:0.9em;grid-gap:1em;box-shadow:0px 0px 5px -3px #000}.contact-card .profile-img-icon{position:relative;height:200px;width:100%}.contact-card .profile-img-icon .profile-img{-webkit-clip-path:inset(0 0 0 0);clip-path:inset(0 0 0 0);transition:-webkit-clip-path 0.5s;transition:clip-path 0.5s;transition:clip-path 0.5s, -webkit-clip-path 0.5s;width:100%;height:100%}.contact-card .profile-img-icon .profile-img img{width:100%}.contact-card .profile-img-icon .job-icon{height:60px;width:60px;position:absolute;left:0;top:83%;-webkit-clip-path:circle(50% at 50% 50%);clip-path:circle(50% at 50% 50%);transition:0.5s}.contact-card .contact-info{margin-top:1.6em;display:grid;grid-gap:0.5em;transition:0.5s;line-height:1em;justify-items:start;align-items:start;width:100%}.contact-card .contact-info .title{font-size:1.2em}.contact-card .contact-info .additional-info{color:grey;font-size:0.8em}.contact-card .contact-info .hover-info{visibility:hidden;opacity:0;font-size:0.8em;color:grey;transition:0.5s}.contact-card:hover .profile-img-icon .profile-img{-webkit-clip-path:inset(0 0 40% 0);clip-path:inset(0 0 40% 0)}.contact-card:hover .profile-img-icon .job-icon{top:48%}.contact-card:hover .contact-info{transform:translateY(-60%)}.contact-card:hover .contact-info .hover-info{visibility:visible;opacity:1;transition-delay:0.2s}.loading{position:fixed;height:100%;width:100%}.search-bar{color:grey;margin-right:1em;border:1px solid #f2f1f2;padding:1em;width:20em}@media screen and (max-width: 555px){.search-bar{width:15em}}@media screen and (max-width: 425px){.search-bar{width:13em}}.search-bar .search-input{border:none;width:93%}@media screen and (max-width: 555px){.search-bar .search-input{width:90%}}

